<template>
            <el-dialog @update:visible="$emit('update:shopVisible',false)" :visible="shopVisible" title="添加店铺" >
                <el-form :model="form">
                    <el-form-item label="店铺名称">
                        <el-input v-model="form.shopName" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="店铺的类别">
                        <el-select v-model="form.shopTypeId" placeholder="请选择">
                            <el-option
                                    key="0"
                                    label="请选择"
                                    value="0"
                            ></el-option>
                            <el-option v-for="item in $store.state.shop.allShopTypeList"
                                       :key="item._id"
                                       :label="item.shopTypeName"
                                       :value="item._id">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="店铺图片">
                        <el-upload
                                :data="form"
                                :on-success="success"
                                class="upload-demo"
                                ref="upload"
                                name="shopPic"
                                :headers="{
                                    authorization:$store.state.admin.token
                                }"
                                action="/ele/addShop"
                                :auto-upload="false">
                            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                        </el-upload>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="$emit('update:shopVisible',false)">取 消</el-button>
                    <el-button type="primary" @click="addShop">确 定</el-button>
                </div>
            </el-dialog>
</template>

<script>
    export default {
        name: "add-shop",
        props: ["shopVisible","shopTypeId"],//接受的参数
        data(){
            return{
                form:{//定义的参数
                    shopName:"",
                    shopTypeId: "0",
                }
            }
        },
        methods:{//方法
            success(){//文件上传时的钩子
                this.$emit("update:shopVisible",false)
                if(this.$route.name === "shopList"){
                    this.$store.dispatch("getShopList",{
                        pageIndex:1
                    })
                }else {
                    this.$router.push("/shopList")
                }
            },
            addShop(){//点击确定后的提交
                this.$refs.upload.submit()
            }
        },
        mounted() {//挂载后
            // this.$store.dispatch("getGoodsList")
            this.$store.dispatch("getAllShopTypeList")
            // console.log(this.$store.state.shop.allShopTypeList);
        },
        created(){//创建后
            // console.log(this.shopTypeId)
            if(this.shopTypeId){
                this.form.shopTypeId = this.shopTypeId;
            }
        },
    }
</script>

